body {
  //background-color: var(--primary-white-color);
  //color: var(--primary-text-color);
  //font-family: Roboto, sans-serif;
  //font-style: normal;
}
h1,
h2,
h3,
h4,
p,
ul {
  margin: 0;
  padding: 0;
}
ul {
  list-style: none;
}
a {
  text-decoration: none;
}
.container {
  padding: 0 15px;
  /*outline: 1px solid red;*/
  margin: 0 auto;
  max-width: 450px;

  @media screen and (min-width: 768px) and (max-width: 1599px) {
    max-width: 738px;
  }

  @media screen and (min-width: 1600px) {
    max-width: 1200px;
  }
}
.section {
  width: 100vw;
  padding-top: 120px;
  padding-bottom: 95px;

  @media screen and (min-width: 1600px) {
    padding-top: 127px;
    padding-bottom: 110px;
  }
}
.visually-hidden {
  visibility: hidden;
  width: 0;
  height: 0;
}
